import React, { createRef, useEffect, useState } from "react";
import ChatMessage from "./ChatMessage";
import firebase from "firebase/app";
import "firebase/firestore";
import "firebase/auth";
import { useCollectionData } from "react-firebase-hooks/firestore";
import fireBaseObject from "../firebaseSetup";
import clsx from "clsx";
import Button from "@material-ui/core/Button";
import { makeStyles, withStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  form: {
    height: "10vh",
    position: "fixed",
    bottom: 0,
    backgroundColor: "rgb(24, 23, 23)",
    width: "100%",
    maxWidth: "728px",
    display: "flex",
    fontSize: "1.5rem",
  },
  main: {
    padding: "10px",
    height: "80vh",
    margin: "10vh 0 10vh",
    overflowY: "scroll",
    display: "flex",
    flexDirection: "column",
    '&::-webkit-scrollbar-thumb': {
      background: "#6649b8",
    },
    '&::-webkit-scrollbar-track': {
      background: "#1e1e24"
    },
    '&::-webkit-scrollbar': {
      width: "0.25rem",
    }
  },
  formButton: {
    width: "20%",
    backgroundColor: "rgb(56, 56, 143)"
  },
  input: {
    lineHeight: "1.5",
    width: "100%",
    fontSize: "1.5rem",
    background: "rgb(58, 58, 58)",
    color: "white",
    outline: "none",
    border: "none",
    padding: "0 10px",
  }
});

function ChatRoom() {
  const firestore = fireBaseObject.firestore;
  const auth = fireBaseObject.auth;
  const messageRef = firestore.collection("messages");
  const query = messageRef.orderBy("createdAt", "asc").limitToLast(25);
  const [messages] = useCollectionData(query, { idField: "id" });
  const [formValue, setFormValue] = useState("");
  const dummy = React.useRef<HTMLInputElement>(null);
  const classes = useStyles();

  // Scroll to the bottom of the messages
  useEffect(() => {
    dummy?.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages])

  const sendMessage = async (e: React.FormEvent) => {
    e.preventDefault();

    if (auth.currentUser && formValue != "" && formValue != null) {
      const { uid, photoURL } = auth.currentUser;

      await messageRef.add({
        text: formValue,
        createdAt: firebase.firestore.FieldValue.serverTimestamp(),
        uid,
        photoURL,
      });

      // Reset form
      setFormValue("");

      dummy?.current?.scrollIntoView({ behavior: "smooth" });
    } else {
      console.log("!!!!!! Send failed due to no current user");
    }
  };

  return (
    <>
      <main className={classes.main}>
        {messages &&
          messages.map((msg) => (
            <ChatMessage key={msg.id} message={msg} auth={auth} />
          ))}
        <div id={"dummy"} ref={dummy}></div>
      </main>

      <form className={classes.form} onSubmit={sendMessage}>
        <input
          value={formValue}
          className={classes.input}
          onChange={(e) => setFormValue(e.target.value)}
        />
        <Button
          variant="contained"
          color="primary"
          type={"submit"}
          className={classes.formButton}
          disabled={formValue === ""}
        >
          Send
        </Button>
      </form>
    </>
  );
}

export default ChatRoom;

function useRef(initialValue: any) {
  throw new Error("Function not implemented.");
}
