import React from "react";
import firebase from "firebase/app";
import "firebase/firestore";
import "firebase/auth";
import clsx from "clsx";
import { makeStyles, withStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
    root: {
    },
    message: {
        display: "flex",
        alignItems: "center",
        textAlign: "left",
        listStyle: "none",
    },
    sent: {
        flexDirection: "row-reverse",
        color: "white",
        background: "#0b93f6",
        alignSelf: "flex-end"
    },
    received: {
        background: "#22e276",
        color: "black",
        alignSelf: "flex-start",
    },
    messageStyle: {
        maxwidth: "500px",
        marginbottom: "12px",
        lineheight: "24px",
        padding: "10px 20px",
        borderradius: "25px",
        position: "relative",
        color: "white",
        textalign: "center",
    },
    image: {
        width: "40px",
        height: "40px",
        borderRadius: "50%",
        margin: "2px 5px",
    },
    date: {
        display: "flex",
        width: "100px",
        padding: "10px",
        paddingBottom: "20px"
    },
    timeSent: {
        flexDirection: "row-reverse",
        color: "white",
        alignSelf: "flex-end"
    },
    timeReceived: {
        color: "black",
        alignSelf: "flex-start",
    },
});

function ChatMessage(props: {
  message: firebase.firestore.DocumentData;
  auth: firebase.auth.Auth;
}) {
  const { text, uid, photoURL, createdAt } = props.message;
  const classes = useStyles();

  const isOwnMessage = uid === props.auth?.currentUser?.uid;

  const messageClass = isOwnMessage ? classes.sent : classes.received;
  const dateClass = isOwnMessage ? classes.timeSent: classes.timeReceived;

  const dateTime = createdAt?.toDate().toLocaleTimeString() + " " + createdAt?.toDate().toLocaleDateString();

  return (
    <>
      <div className={`${classes.message} ${messageClass}`}>
        <img className={classes.image} src={photoURL} />
        <div className={classes.messageStyle}>{text}</div>
      </div>
      <div className={`${classes.date} ${dateClass}`}>
        {dateTime}
      </div>
    </>
  );
}

export default ChatMessage;
